import { Component, OnInit } from '@angular/core';

@Component({
    templateUrl: './refresh-example.component.html',
    styleUrls: ['./refresh-example.component.scss']
})
export class RefreshExampleComponent implements OnInit {
    progress: number = 0;

    list: Array<number> = [];

    private index: number = 0;

    ngOnInit() {
        // ä»æå¡ç«¯è·åæ°æ®
        setTimeout(() => {
            for (let i = 0; i < 30; i++) {
                this.list.push(Math.random());
                this.index = i;
            }
        }, 300);
    }

    dragging(progress: number) {
        this.progress = progress;
    }

    refresh(fn: () => void) {
        // ç¨æ·è§¦åäºä¸æå·æ°
        setTimeout(() => {
            this.list = [];
            for (let i = 0; i < 30; i++) {
                this.list.push(Math.random());
                this.index = i;
            }
            // å·æ°å®æäº
            fn();
        }, 3000);
    }

    infinite(fn: () => void) {
        // ç¨æ·è§¦åäºä¸æå è½½ï¼ä»æå¡ç«¯è·åæ´å¤æ°æ®
        setTimeout(() => {
            let min = this.index;
            let max = min + 30;
            for (; min < max; min++) {
                this.list.push(Math.random());
                this.index = min;
            }
            // å è½½å®æäº
            fn();
        }, 3000);
    }
}


